.form-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 0 22px;
}

.form-container__title {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
}

.form-container__button {
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #2aab6d;
  padding: 23px 45px;
}

.form-container__legend {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
}

.form-container__legend-star {
  color: #d22856;
}

.form-container__desktop-only {
  display: none;
}

@media (min-width: @tablet-width) {
  .form-container__footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: @desktop-width) {
  .form-container__desktop-only {
    display: block;
  }

  .form-container__legend {
    display: flex;
    flex-direction: row;
  }
}
