.achievement__field {
  border: none;
}

.achievement__title {
  font-weight: 400;
  font-size: 14px;
  line-height: 30px;
  color: #d22856;
  text-transform: uppercase;
}

.achievement__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.achievement__item-label {
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
}

@media (min-width: @tablet-width) {
  .achievement__field {
    border: 2px solid #e5e5e5;
    border-radius: 1px;
  }
}

@media (min-width: @desktop-width) {
  .achievement {
    grid-column: 1/-1;
  }
}
